import React from "react";
import { Link } from "gatsby";

const ActionButton = ({
  title,
  className = "",
  handleClick,
  color,
  id = "",
}) => {
  return (
    <div className="rounded-full shadow">
      <button
        id={`shopcarts_${id}-btn`}
        onClick={handleClick}
        className={`w-full flex items-center justify-center px-8 py-2 border border-transparent text-base font-medium rounded-full text-white ${color} md:py-2 md:text-lg md:px-10 ${className}`}
      >
        {title}
      </button>
    </div>
  );
};

const SmallActionButton = ({
  title,
  className = "",
  handleClick,
  color,
  id = "",
}) => {
  return (
    <div className="rounded-full shadow">
      <button
        id={`shopcarts_${id}-btn`}
        onClick={handleClick}
        className={`w-full flex items-center justify-center px-4 py-1 border border-transparent text-base font-medium rounded-full text-white ${color} md:px-3 md:py-1 md:text-md ${className}`}
      >
        {title}
      </button>
    </div>
  );
};

const LinkButton = ({ title, className = "", href, color }) => {
  return (
    <div className="rounded-full shadow">
      <Link
        to={href}
        className={`w-full flex items-center justify-center px-8 py-2 border border-transparent text-base font-medium rounded-full text-white ${color} md:py-2 md:text-lg md:px-10 ${className}`}
      >
        {title}
      </Link>
    </div>
  );
};

export { ActionButton, SmallActionButton, LinkButton };
