import * as React from "react";
import { graphql } from "gatsby";

import Layout from "../Components/Layout";
import ProductCard from "../Components/Shop/ProductCard";

const RenderProducts = ({ products }) => {
  return (
    <div className="flex flex-row flex-wrap justify-around">
      {products.map((product) => (
        <ProductCard product={product} key={product.frontmatter.productID} />
      ))}
    </div>
  );
};

const Shop = ({ data }) => {
  const { allMarkdownRemark } = data;
  return (
    <Layout title="Shop | Avoman">
      <div className="max-w-6xl mx-auto px-5 mb-20">
        <h1 className="text-green-500 text-6xl font-bold">Our offerings</h1>
        <RenderProducts products={allMarkdownRemark.nodes} />
      </div>
    </Layout>
  );
};

export default Shop;

export const pageQuery = graphql`
  query allProducts {
    allMarkdownRemark {
      nodes {
        frontmatter {
          product
          productID
          slug
          price
          featuredImage {
            childImageSharp {
              fluid(maxWidth: 800) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`;
